<script>
import log from "../assets/build.svg";
import register from "../assets/club-logo.png";
export default {
  data: () => ({
    log: log,
    register: register,
    name: null,
    password: null,
  }),
  methods: {
    submit() {
      if (
        this.name.toLowerCase() == "student" ||
        this.name.toLowerCase() == "staff"
      ) {
        localStorage.setItem("name", this.name.toLowerCase());
        this.$router.push("/home");
        this.$vToastify.success("You logged in");
        if (!JSON.parse(localStorage.getItem("task"))) {
          localStorage.setItem(
            "task",
            JSON.stringify([
              {
                id: 0,
                image:
                  "https://www.sodapdf.com/blog/wp-content/uploads/2018/06/best-free-photo-editing-software.jpg",
                name: "Draw Image Over this Image",
                desc:
                  "Lorem, ipsum dolor sit amet consectetur adipisicing elit. Exercitationem quaerat ducimus eum odit pariatur corporis maiores commodi quisquam minus, ab aut delectus est iure sequi nobis debitis cumque praesentium velit.",
                end: "22-02-2021",
                student: [
                  {
                    date: "2021-01-19T14:11:52.880Z",
                    id: 0,
                    image:
                      "https://www.learnupon.com/wp-content/uploads/Video-editing-basics-for-eLearning720x300.png",
                    mark: "2",
                  },
                ],
              },
            ])
          );
        }
      } else {
        this.$vToastify.error("username staff or student only allowed");
      }
    },
  },
  mounted() {
    const sign_in_btn = document.querySelector("#sign-in-btn");
    const sign_up_btn = document.querySelector("#sign-up-btn");
    const container = document.querySelector(".container");

    sign_up_btn.addEventListener("click", () => {
      container.classList.add("sign-up-mode");
    });

    sign_in_btn.addEventListener("click", () => {
      container.classList.remove("sign-up-mode");
    });
  },
};
</script>

